@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  background-color: #1c2930;
  color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 414px;
  margin: 0 auto;
  margin-top: 8vw;
  justify-content: space-around;
  border: 1px solid grey;
  border-radius: 1rem;
  padding: 3rem;
}

form input:not([type='radio']),
form textarea {
  width: 100%;
  padding: 6px 8px;
  border-radius: 6px;
  margin-bottom: 0.5rem;
  border: none;
}

form label {
  display: block;
  font-size: smaller;
  margin-bottom: 0.3rem;
}

form textarea {
  resize: vertical;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  color: #272727;
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  padding: 1rem;
}

a {
  text-decoration: none;
  color: black;
}

li {
  list-style-type: none;
}

button {
  background: dodgerblue;
  padding: 12px 14px; /*This is material UI dimenstion for buttons. Trying to mimic material UI style here*/
  color: white;
  font-weight: bold;
  border-radius: 6px; /*For non-square shaped elements, don't do percent. Always use rem or px*/
  border: none; /*Reset default border. Border is there by html default*/
}

.button-secondary {
  background: white;
  padding: 12px 14px; /*This is material UI dimenstion for buttons. Trying to mimic material UI style here*/
  color: dodgerblue;
  font-weight: bold;
  border-radius: 6px; /*For non-square shaped elements, don't do percent. Always use rem or px*/
  border: 1px solid dodgerblue; /*Reset default border. Border is there by html default*/
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: #eeeeee;
  height: 72px;
  min-height: 60px;
}

.logo {
  height: 40px;
  height: 40px;
}

/*Never resize logo so use absolute size with px. Image should always take full width and height of the container. Only use relative sizing(vw, vw) for layout stuff only. rem is still absolute(16px) em depends on parent size. Don't use em. % depends on parent." */

.nav-left {
  /* width: 10%; */
  display: flex;
  justify-content: flex-end;
}

.list-area {
  -webkit-clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);
          clip-path: polygon(15% 0%, 100% 0%, 85% 100%, 0% 100%);
  background-color: dodgerblue;

  height: 100%;
  line-height: 100%;
  text-align: center;
}

.list-area a {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.list-area:hover {
  background-color: rgb(9, 116, 45);
  transition: 0.2s;
}

.list-text {
  /* vertical-align: middle; */
  /* display: inline-block; */
  /* line-height: 1.5; */
  color: white;
  padding: 0 30px;
}

.property-card {
  width: 100%;
  margin: 0 auto;
}

h1 {
  font-size: 24px;
  font-weight: 400;
  text-align: center;
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

.btn {
  color: #ffffff;
  margin-top: 0.8rem;
  padding: 0.8rem;
  font-size: 14px;
  text-transform: uppercase;
  border-radius: 4px;
  font-weight: 800;
  display: block;
  width: 100%;
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
}

.btn:hover {
  background-color: rgba(255, 255, 255, 0.12);
}

.card {
  background-color: white;
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.card-image {
  height: 164px;
}

.card-image > img {
  width: 100%;
  object-fit: cover;
  height: 100%;
}

.card-content {
  padding: 1rem;
  background: linear-gradient(to bottom left, #ef8d9c 40%, #ffc39e 100%);
}

.address {
  color: #ffffff;
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin: 0px;
}

.card-text {
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1.25rem;
  font-weight: 400;
}

.property-list {
  display: grid;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(264px, 300px));
  grid-auto-flow: dense;
  grid-gap: 28px;
  gap: 28px;
}



.owner-auction-interface {
  display: flex;
  padding: 2rem 0;
  justify-content: space-around;
  /* align-items: center; */
}

.auction-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.address {
  color: black;
  text-align: center;
  margin: 0 auto;
}

.description {
  font-size: 1.5rem;
  text-align: center;
}

.picture-and-auction-info {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
}

.one-picture {
  max-height: 400px;
  max-width: 400px;
  padding: 20px;
}

.auction-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

Modal {
  height: 800px;
  width: 800px;
}

.bid-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1440px;
  height: 96px;
  padding-right: 32px;
  margin-bottom: 20px;
  background: linear-gradient(to bottom left, #ef8d9c 40%, #ffc39e 100%);
  color: black;
  border-radius: 0.25rem;
  box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
  font-size: 16px;
  font-weight: 400;
}

.bid-card:hover {
  cursor: pointer;
}

.current-highest-bid {
  color: 'dodger blue';
  text-transform: uppercase;
}

.property-image {
  width: 96px;
  height: 100%;
  overflow: hidden;
}

.property-image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.green {
  background: green;
  color: white;
  padding: 8px;
}

.red {
  background: red;
  color: white;
  padding: 8px;
}

.my-properties {
  display: grid;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  /* grid-template-columns: repeat(autofill, 1fr); */
  grid-template-columns: repeat(
    auto-fit,
    minmax(264px, 300px)
  ); /*the max value should not be 1fr. If it is, then it takes up the whole page width as a giant 1fr!*/
  grid-auto-flow: dense;
  grid-gap: 28px;
  gap: 28px;
}

@keyframes movedown {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(10px);
  }

  60% {
    transform: translateY(0);
  }
}

.arrow-downward {
  color: #808080;
  animation-name: movedown;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-delay: 1s;
}

.table-container {
  margin: 0 auto;
  border-collapse: collapse;
}

.table-container th,
.table-container td {
  padding: 6px 12px;
  text-align: right;
}

.table-container th {
  font-size: smaller;
}

.table-container thead tr {
  color: white;
  background-color: #1c2930;
}

.table-container tbody tr:hover {
  background-color: #f6f8fa;
}

.by-property-bids {
  /* background-color: yellow; */
  padding: 5rem 1rem;
}

.bids-title {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 1rem;
}

.available {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 1rem;
}

.available > div {
  display: flex;
}

